@font-face {
  font-family: 'Regular';
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/sanspro/sourcesanspro-regular.woff") format('woff'), url("../fonts/sanspro/sourcesanspro-regular.ttf") format('truetype');
}
@font-face {
  font-family: 'Semi-bold';
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/sanspro/sourcesanspro-semibold.woff") format('woff'), url("../fonts/sanspro/sourcesanspro-semibold.ttf") format('truetype');
}
@font-face {
  font-family: 'Bold';
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/sanspro/sourcesanspro-bold.woff") format('woff'), url("../fonts/sanspro/sourcesanspro-bold.ttf") format('truetype');
}
* {
  box-sizing: border-box;
}
body {
  font-family: 'Regular', 'Arial', sans-serif;
  font-size: 16px;
  color: #333;
}
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}
.flex-container {
  margin: 0 -15px;
}
.flex-container:after {
  display: table;
  clear: both;
  content: '';
}
.flex-main {
  float: left;
  width: 70%;
  padding: 0 15px;
}
.flex-aside {
  float: right;
  width: 30%;
  padding: 0 15px;
}
.btn {
  font-size: 16px;
  line-height: normal;
  display: inline-block;
  margin: 0;
  padding: 0;
  cursor: pointer;
  user-select: none;
  transition: all 0.4s;
  text-align: center;
  vertical-align: middle;
  text-decoration: none;
  color: geren;
  border: none;
  border-radius: 3px;
  background-color: transparent;
  box-shadow: none;
}
.btn--full {
  width: 100%;
}
.btn:focus {
  text-decoration: none;
  color: #008000;
  outline: none;
}
.btn:hover {
  text-decoration: none;
  color: #008000;
}
.btn:active {
  text-decoration: none;
  color: #008000;
  outline: none;
}
.btn--lg {
  font-family: 'Semi-bold', 'Arial', sans-serif;
  font-size: 18px;
  padding: 15px;
}
.btn--md {
  font-family: 'Semi-bold', 'Arial', sans-serif;
  font-size: 16px;
  padding: 12px 15px;
}
.btn--sm {
  font-family: 'Semi-bold', 'Arial', sans-serif;
  font-size: 14px;
  padding: 8px 15px;
}
.btn--main {
  color: #fff;
  background-color: #008000;
}
.btn--main:focus {
  color: #fff;
  background-color: #008000;
}
.btn--main:hover {
  color: #fff;
  background-color: #ecd018;
}
.btn--main:active {
  color: #fff;
  background-color: #008000;
}
input[type='text'],
input[type='number'],
input[type='email'],
input[type='search'],
input[type='password'] {
  display: block;
  height: 44px;
  padding: 0 15px;
  transition: all 0.4s;
  color: #333;
  border: 1px solid #333;
  border-radius: 3px;
}
input[type='text']:focus,
input[type='number']:focus,
input[type='email']:focus,
input[type='search']:focus,
input[type='password']:focus {
  border-color: #008000;
  outline: none;
}
input[type='text']:disabled,
input[type='number']:disabled,
input[type='email']:disabled,
input[type='search']:disabled,
input[type='password']:disabled {
  border-color: #808080;
  background-color: #808080;
}
input[type='text'].error,
input[type='number'].error,
input[type='email'].error,
input[type='search'].error,
input[type='password'].error {
  border-color: #f00;
}
.field__label {
  position: relative;
  display: block;
  margin-bottom: 30px;
  text-align: left;
}
.field__input {
  display: block;
  width: 100%;
}
.content__heading {
  font-family: 'Bold', 'Arial', sans-serif;
  font-size: 50px;
  line-height: 1.4;
  margin-bottom: 30px;
  margin-top: 0;
  padding: 20px 0;
  border-top: 2px solid #ecd018;
  border-bottom: 2px solid #ecd018;
  background-image: -webkit-gradient(linear, 0% 0%, 25% 100%, from(#ff2c2c), to(#7a5e91));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.menu {
  margin: 0;
  padding: 0;
  list-style: none;
}
.menu__element {
  display: inline-block;
  margin: 0;
  padding: 0;
  padding: 10px;
  vertical-align: middle;
}
.menu__link {
  font-size: 24px;
  position: relative;
  display: inline-block;
  transition: color 0.4s;
  vertical-align: top;
  text-decoration: none;
  text-transform: uppercase;
  pointer-events: all;
  color: #008000;
}
.menu__link:before {
  position: absolute;
  top: 100%;
  left: 0;
  width: 0;
  height: 3px;
  content: '';
  transition: all 1.2s;
  background-color: #ecd018;
}
.menu__link:hover {
  color: #ecd018;
}
.menu__link:hover:before {
  width: 100%;
  transition: all 0.4s;
  background-color: #008000;
}
.person {
  margin-bottom: 30px;
}
.person__poster {
  height: 200px;
  margin-bottom: 15px;
  text-align: center;
}
.person__img {
  display: inline-block;
  max-width: 100%;
  max-height: 100%;
}
.person__name {
  font-family: 'Bold', 'Arial', sans-serif;
  font-size: 20px;
  margin-bottom: 15px;
  text-align: center;
}
.person__about {
  font-family: 'Semi-bold', 'Arial', sans-serif;
  font-size: 18px;
  margin-bottom: 15px;
}
.person__social {
  text-align: center;
}
.social {
  margin: 0;
  padding: 0;
  list-style: none;
}
.social__element {
  display: inline-block;
  margin: 0;
  padding: 0;
  padding: 10px;
  vertical-align: middle;
}
.social__link {
  font-size: 28px;
  display: inline-block;
  transition: transform 0.4s;
  vertical-align: top;
  text-decoration: none;
  color: #333;
}
.social__link--search {
  margin-right: 30px;
}
.social__link:hover {
  transform: translateY(-15%);
}
.tags {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}
.tags__element {
  display: inline-block;
  margin: 0;
  margin: 10px;
  padding: 0;
  vertical-align: middle;
}
.tags__link {
  font-family: 'Semi-bold', 'Arial', sans-serif;
  font-size: 18px;
  display: inline-block;
  padding: 10px;
  transition: transform 0.4s;
  vertical-align: top;
  text-decoration: none;
  color: #333;
  border-radius: 3px;
  background-color: #ecd018;
}
.tags__link:hover {
  transform: translateY(-15%);
}
.comments__element {
  overflow: hidden;
  margin-bottom: 5px;
  padding: 5px;
  background: #333;
}
.comments__element:last-child {
  margin-bottom: 0;
}
.comments__img {
  float: left;
  width: 50px;
}
.comments__wrapper {
  float: right;
  width: calc(100% - 50px - 1rem);
}
.comments__name {
  font-family: 'Semi-bold', 'Arial', sans-serif;
  color: #fff;
}
.comments__date {
  font-size: 12px;
  font-weight: lighter;
  color: #c3c3c3;
}
.comments__text {
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #fff;
}
.item {
  overflow: hidden;
  max-width: 300px;
  margin: 0 auto;
  border: 1px solid #e3e3e3;
  border-radius: 3px;
  box-shadow: 0 0 10px #e3e3e3;
}
.item__poster {
  height: 150px;
  transition: all 0.4s;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}
.item__body {
  padding: 10px;
}
.item__title {
  font-family: 'Bold', 'Arial', sans-serif;
  font-size: 20px;
  overflow: hidden;
  margin-bottom: 15px;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.item__description {
  overflow: hidden;
  height: 108px;
  margin-bottom: 15px;
  text-align: justify;
}
.item:hover .item__poster {
  background-size: 120% 120%;
}
.rest__subscribe {
  margin: 30px 0;
}
.rest__search {
  margin: 30px 0;
}
.rest__tags {
  margin: 30px 0;
}
.rest__comments {
  margin: 30px 0;
}
.form__search {
  display: flex;
}
.form__search .field__label {
  width: 100%;
  margin-bottom: 0;
}
.list-wrapper {
  display: flex;
  margin: 0 -15px;
  flex-wrap: wrap;
}
.list-element {
  width: 33.333333333333336%;
  margin-bottom: 30px;
  padding: 0 15px;
}
@media all and (max-width: 991px) {
  .list-element {
    width: 50%;
  }
  .tags__element {
    margin: 5px;
  }
  .tags__link {
    font-size: 14px;
    padding: 5px;
  }
}
@media all and (max-width: 768px) {
  .flex-container {
    display: flex;
    flex-wrap: wrap;
  }
  .flex-container:after {
    display: none;
  }
  .flex-main {
    width: 100%;
    order: 2;
  }
  .flex-aside {
    width: 100%;
  }
  .flex-aside--1 {
    order: 1;
  }
  .flex-aside--2 {
    order: 3;
  }
}
@media all and (max-width: 575px) {
  .list-element {
    width: 100%;
  }
  .menu {
    display: none;
  }
  .menu__element {
    display: block;
  }
  #menu:checked ~ .menu {
    position: fixed;
    z-index: 9;
    top: 0;
    left: 0;
    display: block;
    width: 80%;
    height: 100%;
    padding-top: 30px;
    background-color: rgba(0,0,0,0.8);
  }
  #menu:checked ~ .hamburger span {
    background-color: #fff;
  }
  .social__element {
    padding: 0;
  }
  .social__link--search {
    position: absolute;
    top: 15px;
    right: 15px;
    margin: 0;
  }
}
.header__main {
  display: flex;
  margin-bottom: 15px;
  padding: 15px 0;
  justify-content: space-between;
}
.header__logo-main {
  margin: 0;
}
.header__logo-link {
  display: block;
  max-width: 160px;
  margin: 0 auto;
  padding-top: 90px;
  text-align: center;
  text-decoration: none;
  letter-spacing: 5px;
  color: #333;
  background-image: url("../images/crown-yellow.png");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: 100%;
}
.header__nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}
.label-menu input {
  display: none;
}
.hamburger {
  display: none;
}
@media all and (max-width: 575px) {
  .header__main {
    flex-wrap: wrap;
    justify-content: center;
  }
  .header__logo {
    width: 100%;
    margin-bottom: 15px;
  }
  .header__nav {
    width: 100%;
    align-items: center;
  }
  .header__social .social__element:not(:first-child) {
    display: none;
  }
  .hamburger {
    position: absolute;
    z-index: 11;
    top: 15px;
    left: 15px;
    display: flex;
    flex-direction: column;
    width: 24px;
    height: 16px;
    cursor: pointer;
    text-align: center;
    pointer-events: fill;
    align-self: center;
    justify-content: space-between;
  }
  .hamburger span {
    display: block;
    width: 100%;
    height: 2px;
    background-color: #333;
  }
}
.footer {
  padding: 30px 0;
  text-align: center;
  border-top: 2px solid #ecd018;
}
.footer__logo {
  display: inline-block;
  max-width: 150px;
  margin-bottom: 30px;
  vertical-align: top;
}
.footer__logo-img {
  display: inline-block;
  max-width: 100%;
}
.footer__copy {
  font-size: 14px;
  color: #333;
}